.s-home {
  background-color: var(--color-gray-18);
  width: 100%;
  height: 100vh;
  min-height: calc(25.5 * var(--space));
  overflow: hidden;
  position: relative;
}

.s-home__bg {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  /* background-image: url(../images/background_image.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover; */
  background-color: white;
}

/* ------------------------------------------------------------------- 
   * ## home content
   * ------------------------------------------------------------------- */
.s-home__content {
  height: 100%;
  -ms-flex-align: center;
  -webkit-box-align: center;
  align-items: center;
  text-align: center;
  padding-top: 16vh;
  padding-bottom: 12vh;
  position: relative;
}

.s-home__content img {
  width: 200px;
  height: 200px;
  margin-bottom: 0px;
  border: 10px var(--color-gray-1) solid;
  border-radius: 50%;
}
.s-home__content h1 {
  font-size: 9.8rem;
  font-weight: 600;
  letter-spacing: -0.04em;
  line-height: 1;
  color: var(--color-gray-12);
  margin-top: 0;
  margin-bottom: 0;
}

.s-home__content h3 {
  font-family: inherit;
  font-weight: 400;
  font-size: 2rem;
  line-height: 1.667;
  color: var(--color-gray-10);
  margin-top: var(--vspace-0_5);
  margin-right: auto;
  margin-left: auto;
  padding-bottom: var(--vspace-1);
  max-width: 800px;
  position: relative;
  line-height: 1.3;
  margin-bottom: 0px;
}

.s-home__content h3 a {
  box-shadow: inset 0 -8px 0 rgb(251 177 215 / 60%);
}

.s-home__content h3::after {
  display: block;
  content: "";
  text-align: center;
  height: 1px;
  width: 400px;
  background-color: var(--color-gray-1);
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%);
  position: absolute;
  bottom: 0;
  left: 50%;
}

.s-home__content-social {
  display: -ms-inline-flexbox;
  display: -webkit-inline-box;
  display: inline-flex;
  -ms-flex-flow: row wrap;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  flex-flow: row wrap;
  font-size: 3rem;
  line-height: 1;
  margin-top: var(--vspace-0_5);
}

.s-home__content-social a {
  color: var(--color-gray-10);
  margin-right: var(--vspace-0_75);
}

.s-home__content-social a:hover,
.s-home__content-social a:focus {
  color: var(--color-pink);
}

.s-home__content-social a:last-child {
  margin-right: 0;
}

/* ------------------------------------------------------------------- 
   * ## home scrolldown
   * ------------------------------------------------------------------- */
.s-home__scroll {
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%);
  position: absolute;
  left: 50%;
  bottom: var(--vspace-1_25);
}

.s-home__scroll-link {
  display: -ms-flexbox;
  display: -webkit-box;
  display: flex;
  -ms-flex-flow: row wrap;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  flex-flow: row wrap;
  font-weight: 400;
  font-size: calc(var(--text-size) * 0.7);
  line-height: var(--vspace-1);
  text-transform: uppercase;
  letter-spacing: 0.4em;
  color: var(--color-gray-10);
}

.s-home__scroll-link:hover .scroll-text::after,
.s-home__scroll-link:focus .scroll-text::after {
  width: 100%;
}

.s-home__scroll-link:hover .scroll-arrow svg path {
  fill: var(--color-pink);
}
.s-home__scroll-link:hover .scroll-text {
  color: var(--color-pink);
  transition: all 0.3s ease-in-out;
}

.s-home__scroll-link .scroll-arrow {
  font-size: 30px;
  margin-right: 10px;
  cursor: pointer;
}

.s-home__scroll-link .scroll-arrow svg path {
  fill: var(--color-gray-10);
  transition: all 0.3s ease-in-out;
}

.s-home__scroll-link .scroll-text {
  position: relative;
  cursor: pointer;
}

.s-home__scroll-link .scroll-text::after {
  content: "";
  display: block;
  width: 0;
  height: 1px;
  background-color: white;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  position: absolute;
  bottom: 0;
  left: 0;
}

/* ------------------------------------------------------------------- 
 * responsive:
 * home
 * ------------------------------------------------------------------- */

@media screen and (max-width: 1200px) {
  .s-home__content h1 {
    font-size: 8.4rem;
  }
  .s-home__content h3 {
    font-size: 1.9rem;
  }
}

@media screen and (max-width: 900px) {
  .s-home__content h1 {
    font-size: 7.6rem;
  }
  .s-home__content h3 {
    font-size: 1.8rem;
  }
  .s-home__content-social {
    font-size: 2.4rem;
  }
}

@media screen and (max-width: 800px) {
  .s-home__content {
    padding-top: 12vh;
  }
  .s-home__content h1 {
    font-size: 6.8rem;
  }
  .s-home__content h3 {
    max-width: 600px;
  }
}

@media screen and (max-width: 700px) {
  .s-home__content h1 {
    font-size: 6.2rem;
  }
}

@media screen and (max-width: 600px) {
  .s-home__content h1 {
    font-size: 5.8rem;
  }
  .s-home__content-social {
    font-size: 2.2rem;
  }
}

@media screen and (max-width: 500px) {
  .s-home__content h1 {
    font-size: 5.2rem;
  }
}

@media screen and (max-width: 400px) {
  .s-home__content h1 {
    font-size: 4.8rem;
  }
  .s-home__content h3 {
    font-size: 1.7rem;
  }
  .s-home__content h3::after {
    width: 240px;
  }
  .s-home__content-social {
    font-size: 2rem;
  }
  .s-home__content-social a {
    margin-right: var(--vspace-0_5);
  }
}
