@media screen and (max-width: 1000px) {
  .s-footer {
    padding-bottom: var(--vspace-2);
  }
  .s-footer__social {
    margin-left: 0;
    margin-bottom: var(--vspace-0_5);
    top: 0;
  }
  .ss-copyright {
    -webkit-order: 0;
            order: 0;
  }
}

@media screen and (max-width: 800px) {
  .ss-go-top a {
    height: 5.2rem;
    width: 5.2rem;
  }
  .ss-go-top svg {
    height: 2rem;
    width: 2rem;
  }
}

@media screen and (max-width: 600px) {
  .ss-copyright span {
    display: block;
  }
  .ss-copyright span::after {
    display: none;
  }
  .ss-go-top {
    right: 2.4rem;
  }
}

@media screen and (max-width: 500px) {
  .s-footer__social {
    font-size: var(--text-md);
  }
  .s-footer__social li {
    margin-right: 1.4rem;
  }
}

